import createScreen from '../util/createScreen';
import { createTestScreen } from './testScreen';
import { AlignMode } from '../util/loadFontFile';
import { createBatch, InputHandler, createOrthoCamera } from 'gdxjs';
// import createWhiteTex from 'gl-white-texture';
export const createInstructionScreen = async Game => {
  const goToGameScreen = async () => {
    Game.setCurrentScreen(await createTestScreen(Game));
  };
  // document.addEventListener('keydown', goToGameScreen);

  const { gl, width, height, canvas, font } = Game;
  const inputHandler = new InputHandler(canvas);
  const instruction = Game.assets.instruction;
  const instrucW = width;
  const instrucH = (instrucW * instruction.height) / instruction.width;
  const batch = createBatch(gl);
  const camera = createOrthoCamera(width, height, width, height);
  // const whiteTex = createWhiteTex(gl);

  const text1 = font.setNewText(width);
  const text2 = font.setNewText(width);
  const text3 = font.setNewText(width);
  const fontsize = width / 30;
  // inputHandler.addEventListener('touchStart', (x, y) => {
  //   goToGameScreen();
  // });

  let stateTime = 0;
  let startCount = true;
  return createScreen({
    update(delta) {
      if (startCount) {
        stateTime += delta;
      }
      if (stateTime >= 1) {
        stateTime = 0;
        inputHandler.addEventListener('touchStart', (x, y) => {
          startCount = false;
          goToGameScreen();
        });
      }
      camera.setPosition(0, 0);
      batch.setProjection(camera.combined);
      batch.begin();
      batch.setColor(1, 1, 1, 1);
      batch.draw(instruction, width / 2 - instrucW / 2, height / 2 - instrucH / 2, instrucW, instrucH);

      text1.setFixedGlyphWidth(width / 3);
      text1.draw(batch, 'NHN VO MN HNH  NHY', (width * 2) / 3, height / 7, fontsize);

      text2.setAlignMode(AlignMode.right);
      text2.setFixedGlyphWidth(width / 2.7);
      text2.draw(batch, 'CHM NHNG VT TRN KHNG C TH THU C MT LNG VNG', width / 2.6, height / 2.6, fontsize);

      text3.setFixedGlyphWidth(width / 3);
      text3.draw(batch, 'THU THP  S LNG NG H S GIP BN S DNG K NNG C BIT', (width * 1.9) / 3, (height * 2.8) / 4, fontsize);
      batch.end();
    },
    destroy() {
      inputHandler.cleanup();
      // document.removeEventListener('keydown', goToGameScreen);
    }
  });
};
