import createScreen from '../util/createScreen';
import { TextType } from '../util/loadFontFile';
import { createBatch, createOrthoCamera } from 'gdxjs';
export const renderCoin = (Game, coinCount) => {
  const { gl, width, height, font } = Game;

  const coinImage = Game.assets.coin;
  const coinW = width / 17;
  const coinH = (coinW * coinImage.height) / coinImage.width;
  const text = font.setNewText(width);

  const batch = createBatch(gl);
  const camera = createOrthoCamera(width, height, width, height);

  return createScreen({
    update(coinCount) {
      camera.setPosition(0, 0);
      batch.setProjection(camera.combined);
      batch.begin();
      batch.draw(coinImage, (width * 3.8) / 5, (height * 1) / 30, coinW, coinH);
      // text.setFixedGlyphWidth(width / 10);
      text.setType(TextType.dynamic);
      batch.setColor(102 / 255, 51 / 255, 0, 1);
      text.draw(batch, `${coinCount}`, (width * 4.15) / 5, (height * 1) / 30, width / 20);
      batch.setColor(1, 1, 1, 1);
      batch.end();
    },
    destroy() {}
  });
};
