import { fillRect, createAnimation } from 'gdxjs';
import { PlayMode } from 'gdxjs/lib/createAnimation';

const OBSTACLE_NAMES = [null, 'bed', 'bath', 'tivi'];

export const createObstacleRenderSystem = (whiteTex, batch, obstacles, obstacleAtlas) => {
  const animations = [];
  const idleAnimations = [];
  const bulletAnimations = [];

  const projectileHitAnimation = createAnimation(0.05, obstacleAtlas.findRegions('bullet_hit').slice(1, -1));
  const coinAnimation = createAnimation(0.05, obstacleAtlas.findRegions('coin'));

  for (let i = 0; i < OBSTACLE_NAMES.length; i++) {
    if (i === 0) continue;
    const frames = obstacleAtlas.findRegions(OBSTACLE_NAMES[i]);
    animations[i] = createAnimation(0.1, frames);
    idleAnimations[i] = createAnimation(0.1, frames.slice(0, 4));
    bulletAnimations[i] = createAnimation(0.1, obstacleAtlas.findRegions(`${OBSTACLE_NAMES[i]}_bullet`));
  }

  return {
    draw(stateTime) {
      for (let obstacle of obstacles) {
        const scaleX = obstacle.wallIndex === 0 ? -1 : 1;
        if (obstacle.type === 0) {
          batch.setColor(0, 0, 0, 0.75);
          !obstacle.hit && fillRect(batch, whiteTex, obstacle.x, obstacle.y, obstacle.width, obstacle.height);
          batch.setColor(1, 1, 1, 1);
        } else {
          if (obstacle.fired) {
            !obstacle.hit &&
              animations[obstacle.type]
                .getKeyFrame(stateTime - obstacle.projectileStart, PlayMode.NORMAL)
                .draw(batch, obstacle.x, obstacle.y, obstacle.width, obstacle.height, obstacle.width / 2, obstacle.height, 0, scaleX * 1.5, 1.5);
            if (obstacle.projectileHit) {
              if (stateTime - obstacle.projectileHitTime <= 11 * 0.05) {
                batch.setColor(0, 0, 0, 0.25);
                projectileHitAnimation
                  .getKeyFrame(stateTime - obstacle.projectileHitTime, PlayMode.NORMAL)
                  .draw(
                    batch,
                    obstacle.projectilePos.x - obstacle.projectileSize,
                    obstacle.projectilePos.y,
                    obstacle.projectileSize * 2,
                    obstacle.projectileSize,
                    obstacle.projectileSize,
                    obstacle.projectileSize / 2,
                    0,
                    1.5,
                    1.5
                  );
                batch.setColor(1, 1, 1, 1);
                projectileHitAnimation
                  .getKeyFrame(stateTime - obstacle.projectileHitTime, PlayMode.NORMAL)
                  .draw(
                    batch,
                    obstacle.projectilePos.x - obstacle.projectileSize + obstacle.projectileSize * 0.05,
                    obstacle.projectilePos.y + obstacle.projectileSize * 0.05,
                    obstacle.projectileSize * 2,
                    obstacle.projectileSize,
                    obstacle.projectileSize,
                    obstacle.projectileSize / 2,
                    0,
                    1.5,
                    1.5
                  );
                coinAnimation
                  .getKeyFrame(stateTime - obstacle.projectileHitTime, PlayMode.NORMAL)
                  .draw(
                    batch,
                    obstacle.projectilePos.x,
                    obstacle.projectilePos.y + obstacle.projectileSize / 2,
                    obstacle.projectileSize,
                    (obstacle.projectileSize / 8) * 10,
                    obstacle.projectileSize / 2,
                    0,
                    0,
                    2,
                    2
                  );
              }
            } else {
              bulletAnimations[obstacle.type]
                .getKeyFrame(stateTime, PlayMode.LOOP_PINGPONG)
                .draw(
                  batch,
                  obstacle.projectilePos.x - obstacle.projectileSize / 2,
                  obstacle.projectilePos.y - obstacle.projectileSize / 2,
                  obstacle.projectileSize,
                  obstacle.projectileSize,
                  obstacle.projectileSize / 2,
                  obstacle.projectileSize / 2,
                  0,
                  1.2,
                  1.2
                );
            }
          } else {
            !obstacle.hit &&
              idleAnimations[obstacle.type]
                .getKeyFrame(stateTime, PlayMode.LOOP_PINGPONG)
                .draw(batch, obstacle.x, obstacle.y, obstacle.width, obstacle.height, obstacle.width / 2, obstacle.height, 0, scaleX * 1.5, 1.5);
          }
        }
      }
    }
  };
};
