import { createBatch, createOrthoCamera } from 'gdxjs';

export const createBuffListRenderSystem = Game => {
  const { gl, width, height, assets } = Game;
  const watchAtlas = assets.watchAtlas;
  const batch = createBatch(gl);
  const camera = createOrthoCamera(width, height, width, height);
  let watchList = [];
  for (let i = 0; i < 3; i++) {
    watchList.push(watchAtlas.findRegion(`watch_${i + 1}`, 0));
  }
  let watchSize = width / 8.5;
  let tmpDistance = 0;
  return {
    update(buffs) {
      camera.setPosition(0, 0);
      batch.setProjection(camera.combined);
      batch.begin();
      batch.setColor(1, 1, 1, 1);
      for (let i = 0; i < buffs.length; i++) {
        if (buffs[i].state === 'zoomout') {
          buffs[i].scale -= 0.1;
          if (buffs[i].scale <= 0) {
            buffs[i].state = 'visible';
          }
        }
        if (buffs[i].state === 'enable' || buffs[i].state === 'zoomout') {
          watchList[buffs[i].type].draw(
            batch,
            width / 8 + tmpDistance - watchSize / 2,
            height / 18 - watchSize / 2,
            watchSize,
            watchSize,
            watchSize / 2,
            watchSize / 2,
            0,
            buffs[i].scale,
            buffs[i].scale
          );
        }
        if (buffs[i].state === 'visible') {
          buffs.splice(i, 1);
        }
        tmpDistance += watchSize;
      }
      tmpDistance = 0;
      batch.end();
    }
  };
};
