import { fillRect } from 'gdxjs';

export const createWallRenderSystem = (width, height, whiteTex, batch, wallStarts, WALL_LENGTH, WALL_THICKNESS) => {
  return {
    draw() {
      batch.setColor(0, 0, 0, 0.75);
      for (let wallStart of wallStarts) {
        fillRect(batch, whiteTex, 0, wallStart - WALL_LENGTH, WALL_THICKNESS, WALL_LENGTH);
        fillRect(batch, whiteTex, width - WALL_THICKNESS, wallStart - WALL_LENGTH, WALL_THICKNESS, WALL_LENGTH);
      }
      batch.setColor(1, 1, 1, 1);
    }
  };
};
