import './index.css';

import { resizeCanvas, createGameLoop, loadTexture, loadAtlas } from 'gdxjs';
import { createGenderChoosingScreen } from './screen/genderChoosingScreen';
import loadFontFile from './util/loadFontFile';
const init = async () => {
  const canvas = document.getElementById('main');
  const loading = document.getElementById('loading');
  const [width, height] = resizeCanvas(canvas, 2);
  const gl = canvas.getContext('webgl');
  // const font = await loadFontFile(gl, './arial-32px.fnt');
  const font = await loadFontFile(gl, './myriad.fnt');
  const title = await loadFontFile(gl, './tittle.fnt');

  const Game = {
    title,
    font,
    width,
    height,
    canvas,
    gl,
    pixelRatio: 2,
    setCurrentScreen(screen) {
      if (Game.currentScreen) {
        Game.currentScreen.destroy();
      }
      Game.currentScreen = screen;
    }
  };

  let count = 0;
  loading.innerHTML = `Loading (${count} / 7)...`;
  const doAndCount = promise =>
    promise.then(result => {
      count++;
      loading.innerHTML = `Loading (${count} / 20)...`;
      return result;
    });

  const [
    bg1,
    bg2,
    deadLineAtlas,
    characterAtlas,
    slashAtlas,
    obstacleAtlas,
    noteTex,
    watchAtlas,
    man,
    woman,
    tray,
    endScreen,
    achive1,
    achive2,
    achive3,
    manjump,
    instruction,
    coin,
    button,
    retry,
    reward
  ] = await Promise.all([
    doAndCount(loadTexture(gl, './test-bg.png')),
    doAndCount(loadTexture(gl, './test-bg2.jpg')),
    doAndCount(loadAtlas(gl, './deadline.atlas')),
    doAndCount(loadAtlas(gl, './test.atlas')),
    doAndCount(loadAtlas(gl, './slash.atlas')),
    doAndCount(loadAtlas(gl, './obstacle.atlas')),
    doAndCount(loadTexture(gl, './note.png')),
    doAndCount(loadAtlas(gl, './watch.atlas')),
    doAndCount(loadTexture(gl, './man.png')),
    doAndCount(loadTexture(gl, './woman.png')),
    doAndCount(loadTexture(gl, './tray.png')),
    doAndCount(loadTexture(gl, './end-screen.jpg')),
    doAndCount(loadTexture(gl, './archivement_1.png')),
    doAndCount(loadTexture(gl, './archivement_2.png')),
    doAndCount(loadTexture(gl, './archivement_3.png')),
    doAndCount(loadTexture(gl, './man jump.png')),
    doAndCount(loadTexture(gl, './instruction.png')),
    doAndCount(loadTexture(gl, './coin.png')),
    doAndCount(loadTexture(gl, './button.png')),
    doAndCount(loadTexture(gl, './retry-btn.png')),
    doAndCount(loadTexture(gl, './reward-btn.png'))
  ]);
  const assets = {
    bg1,
    bg2,
    deadLineAtlas,
    characterAtlas,
    slashAtlas,
    obstacleAtlas,
    noteTex,
    watchAtlas,
    man,
    woman,
    tray,
    endScreen,
    achive1,
    achive2,
    achive3,
    manjump,
    instruction,
    coin,
    button,
    retry,
    reward
  };
  Game.assets = assets;

  Game.setCurrentScreen(await createGenderChoosingScreen(Game));
  loading.style.display = 'none';

  gl.clearColor(1, 1, 1, 1);
  const update = delta => {
    gl.clear(gl.COLOR_BUFFER_BIT);
    if (Game.currentScreen) {
      Game.currentScreen.update(delta);
    }
  };

  createGameLoop(update);
};

init();
