import { createAnimation } from 'gdxjs';
import { PlayMode } from 'gdxjs/lib/createAnimation';

export const createPlayerRenderSystem = (width, height, batch, characterAtlas, slashAtlas, obstacleAtlas, watchAtlas, PLAYER_SIZE, gender, outfit) => {
  let runAnimationName = `${gender === 0 ? 'man' : 'girl'}_running_${outfit + 1}`;
  let jumpAnimationName = `${gender === 0 ? 'man' : 'girl'}_jump_${outfit + 1}`;
  let runFrames = characterAtlas.findRegions(runAnimationName);
  let jumpFrames = characterAtlas.findRegions(jumpAnimationName);
  let runAnimation = createAnimation(0.05, runFrames);
  let jumpAnimation = createAnimation(0.1, jumpFrames);

  const updateOutfit = outfit => {
    runAnimationName = `${gender === 0 ? 'man' : 'girl'}_running_${outfit + 1}`;
    jumpAnimationName = `${gender === 0 ? 'man' : 'girl'}_jump_${outfit + 1}`;
    runFrames = characterAtlas.findRegions(runAnimationName);
    jumpFrames = characterAtlas.findRegions(jumpAnimationName);
    runAnimation = createAnimation(0.05, runFrames);
    jumpAnimation = createAnimation(0.1, jumpFrames);
  };

  const transitionAnimation = createAnimation(0.05, obstacleAtlas.findRegions('bullet_hit').slice(1, -1));
  const slashFrames = slashAtlas.findRegions('slash');
  const slashAnimation = createAnimation(0.03, slashFrames);
  const buffAnimation = createAnimation(0.1, watchAtlas.findRegions('watch_buff'));

  const SLASH_X = width * 0.1;
  const SLASH_WIDTH = width * 0.8;
  const SLASH_HEIGHT = (SLASH_WIDTH * slashFrames[0].height) / slashFrames[0].width;
  let firstJump = true;

  return {
    draw(x, y, flipped, jumping, stateTime, jumpStart, buffTime, currentOutfit, transitionTime, specialTime) {
      if (outfit !== currentOutfit) {
        outfit = currentOutfit;
        updateOutfit(outfit);
      }
      const originX = PLAYER_SIZE / 2;
      const originY = 0;
      const scaleX = flipped ? 1 : -1;

      if (transitionTime > stateTime - 0.1) {
        batch.setColor(0.478, 0.988, 1, 1);
        transitionAnimation.getKeyFrame(stateTime - transitionTime, PlayMode.LOOP_PINGPONG).draw(batch, x, y, PLAYER_SIZE * 2, PLAYER_SIZE);
        batch.setColor(1, 1, 1, 1);
        return;
      }

      if (!firstJump) {
        slashAnimation
          .getKeyFrame(stateTime - jumpStart, PlayMode.NORMAL)
          .draw(batch, SLASH_X, y, SLASH_WIDTH, SLASH_HEIGHT, SLASH_WIDTH / 2, SLASH_HEIGHT / 2, 0, scaleX, 1.5);
      }

      if (!jumping) {
        runAnimation.getKeyFrame(stateTime, PlayMode.LOOP).draw(batch, x, y, PLAYER_SIZE, PLAYER_SIZE, originX, originY, 0, scaleX, 1);
      } else {
        firstJump = false;
        jumpAnimation
          .getKeyFrame(stateTime - jumpStart, PlayMode.NORMAL)
          .draw(batch, x, y, (PLAYER_SIZE * 150) / 115, PLAYER_SIZE, originX, originY, 0, scaleX, 1);
      }

      // buff animation
      if (buffTime >= stateTime) {
        const frame = buffAnimation.getKeyFrame(stateTime - (buffTime - 2.5), PlayMode.LOOP_PINGPONG);
        frame && frame.draw(batch, x, y, PLAYER_SIZE, PLAYER_SIZE, PLAYER_SIZE / 2, PLAYER_SIZE / 2, 0, 1.5, 1.5);
      }

      if (specialTime >= stateTime) {
        const frame = buffAnimation.getKeyFrame(stateTime - (specialTime - 2.5), PlayMode.LOOP_PINGPONG);
        frame && frame.draw(batch, x, y, PLAYER_SIZE, PLAYER_SIZE, PLAYER_SIZE / 2, PLAYER_SIZE / 2, 0, 1.5, 1.5);
      }
    }
  };
};
