import createScreen from '../util/createScreen';
import { InputHandler, createBatch, createOrthoCamera } from 'gdxjs';
import createWhiteTex from 'gl-white-texture';
import { createDeadlineRenderSystem } from './system/DeadlineRenderSystem';
import { createBgRenderSystem } from './system/BgRenderSystem';
import { createParticleSystem } from './system/ParticleSystem';
import { createWallSystem } from './system/WallSystem';
import { createWallRenderSystem } from './system/WallRenderSystem';
import { createPlayerRenderSystem } from './system/PlayerRenderSystem';
import { createObstacleSystem } from './system/ObstacleSystem';
import { createObstacleRenderSystem } from './system/ObstacleRenderSystem';
import { createDebuffRenderSystem } from './system/DebuffRenderSystem';
import { createWatchSystem } from './system/WatchSystem';
import { createWatchRenderSystem } from './system/WatchRenderSystem';
import { createEndScreen } from './genderEndScreen';
import { renderCoin } from './coinRenderSystem';
import { createBuffListRenderSystem } from './system/BuffListRenderSystem';
import { checkPointInsideRect } from '../util/coordUtil';

// import { specialButton } from './system/specialRenderSystem';
import { specialButton } from './specialRenderSystem';
export const createTestScreen = async Game => {
  const { gender, gl, width, height, canvas, assets, pixelRatio } = Game;
  let outfit = 0;

  const batch = createBatch(gl);
  const camera = createOrthoCamera(width, height, width, height);
  const whiteTex = createWhiteTex(gl);
  let inputHandler = new InputHandler(canvas);
  const { bg1, bg2, deadLineAtlas, characterAtlas, slashAtlas, obstacleAtlas, noteTex, watchAtlas } = assets;

  const BG_WIDTH = width;
  const BG1_HEIGHT = (BG_WIDTH / bg1.width) * bg1.height;

  const WALL_THICKNESS = width * 0.05;
  const WALL_LENGTH = BG1_HEIGHT / 7;
  const JUMP_SPEED_X = width * 2;
  const NORMAL_SPEED_Y = -height * 0.75;
  const PLAYER_SIZE = width / 5;
  const INITIAL_GAP = height * 0.5;

  let buffTime = 0;
  let specialTime = 0;
  let coinCount = 0;
  let watchCount = 0;
  const hasBuff = () => buffTime >= stateTime;
  const hasSpecial = () => specialTime >= stateTime;
  let x = WALL_THICKNESS;
  let y = 0;
  let speedX = 0;
  let speedY = NORMAL_SPEED_Y;
  let targetGap = INITIAL_GAP;
  let gap = INITIAL_GAP;
  let flipped = false;
  let jumping = false;
  const minX = WALL_THICKNESS;
  const maxX = width - WALL_THICKNESS - PLAYER_SIZE;
  let jumpStart = 0;

  const wallStarts = [];
  const debuffs = [];
  const buffs = [];

  const checkTouch = (x, y) => {
    let useSpecial = false;
    if (buffs.length === 3 && buffs[0].state === 'enable') {
      const buttonR = width / 3.8;
      const button_rect = [
        width / 2 - buttonR / 2,
        (height * 4.7) / 6,
        width / 2 - buttonR / 2 + buttonR,
        (height * 4.7) / 6,
        width / 2 - buttonR / 2,
        (height * 4.7) / 6 + buttonR,
        width / 2 - buttonR / 2 + buttonR,
        (height * 4.7) / 6 + buttonR
      ];
      let point = { x: x, y: y };
      if (checkPointInsideRect(point, button_rect, pixelRatio)) {
        specialTime = stateTime + 2.5;
        targetGap = targetGap + (INITIAL_GAP - targetGap) * 0.5;
        for (let buff of buffs) {
          buff.state = 'zoomout';
        }
        useSpecial = true;
      }
    }
    !useSpecial && jump();
  };

  const jump = () => {
    if (jumping || debuffs.length > 0) {
      return;
    }
    speedX = flipped ? -JUMP_SPEED_X : JUMP_SPEED_X;
    jumping = true;
    jumpStart = stateTime;
  };

  inputHandler.addEventListener('touchStart', checkTouch);
  document.addEventListener('keydown', jump);

  const checkLanding = newX => {
    if (!jumping) {
      return;
    }
    let landing = false;
    //
    if (newX > maxX) {
      x = maxX;
      landing = true;
    }

    if (newX < minX) {
      x = minX;
      landing = true;
    }

    if (landing) {
      speedX = 0;
      jumping = false;
      flipped = !flipped;
    }
    return landing;
  };

  const processPlayerPos = delta => {
    let modifier = 1;
    if (debuffs.length > 0) {
      modifier = 0.5;
    }
    if (hasSpecial()) {
      modifier *= 1.5;
    }
    y += speedY * delta * modifier;
    const deltaX = speedX * delta;
    if (!checkLanding(x + deltaX)) {
      x += deltaX;
    }
  };

  const processWatchCollsion = delta => {
    const pL = x;
    const pR = x + PLAYER_SIZE;
    const pT = y;
    const pB = y + PLAYER_SIZE;

    for (let watch of watches) {
      if (watch.hit) continue;

      const oL = watch.x - watch.width / 2;
      const oR = watch.x + watch.width / 2;
      const oT = watch.y - watch.height / 2;
      const oB = watch.y + watch.height / 2;
      if (pL < oR && pR > oL && pT < oB && pB > oT) {
        watchCount += 1;
        watch.hit = true;
        if (buffs.length <= 2) {
          buffs.push({ type: watch.type, stateTime: 0, state: 'enable', scale: 1 });
        }
        watch.hitTime = stateTime;
      }
    }
  };

  const processObstacleCollision = delta => {
    const pL = x;
    const pR = x + PLAYER_SIZE;
    const pT = y;
    const pB = y + PLAYER_SIZE;

    let hitted = false;

    for (let obstacle of obstacles) {
      if (obstacle.hit) continue;
      const oWidth = obstacle.width;
      const oHeight = obstacle.height;
      const oL = obstacle.x;
      const oR = obstacle.x + oWidth;
      const oT = obstacle.y;
      const oB = obstacle.y + oHeight;

      if (pL < oR && pR > oL && pT < oB && pB > oT && !jumping) {
        obstacle.hit = true;
        if (buffs.length > 0) {
          buffTime = stateTime + 0.2;
          // targetGap = targetGap + (INITIAL_GAP - targetGap) * 0.5;
          buffs[0].state = 'zoomout';
          // buffs.shift();
        }
        if (!hasBuff() && !hasSpecial()) {
          hitted = true;
          debuffs.push({
            time: stateTime,
            duration: 1,
            type: obstacle.type,
            x: width / 2 + ((Math.random() - 0.5) * width) / 20,
            offsetY: -((Math.random() * width) / 20 + width / 40),
            flipped: flipped
          });
        }
      }
    }

    for (let obstacle of obstacles) {
      if (!obstacle.fired || obstacle.projectileHit) continue;
      const pos = obstacle.projectilePos;
      const size = obstacle.projectileSize;

      const oL = pos.x - size / 3;
      const oR = pos.x + size / 3;
      const oT = pos.y - size / 3;
      const oB = pos.y + size / 3;

      if (pL < oR && pR > oL && pT < oB && pB > oT) {
        obstacle.projectileHit = true;
        obstacle.projectileHitTime = stateTime;
        obstacle.projectileVel.set(0, speedY);
        if (!jumping && !hasBuff() && !hasSpecial() && buffs.length === 0) {
          debuffs.push({
            time: stateTime,
            duration: 1,
            type: obstacle.type,
            x: width / 2 + ((Math.random() - 0.5) * width) / 10,
            offsetY: -((Math.random() * width) / 10 + width / 20),
            flipped: flipped
          });
          obstacle.projectileVel.set(0, speedY / 2);
          hitted = true;
        } else {
          if (buffs.length > 0 && !jumping) {
            buffTime = stateTime + 0.2;
            buffs[0].state = 'zoomout';
          }
          targetGap = targetGap + (INITIAL_GAP - height) * 0.1;
          targetGap = Math.max(targetGap, INITIAL_GAP);
          coinCount += 5;
        }
      }
    }
    if (hitted) {
      targetGap = targetGap + (height - INITIAL_GAP) * 0.2;
      const rate = deadlineRenderSystem.getDeadLineParticleRate();
      deadlineRenderSystem.setDeadLineParticleRate(rate + (MAXIMUM_DEADLINE_RATE - rate) * 0.2);
    }
  };

  let transitionTime = -2;

  const OUTFIT_COIN_REQUIRED = [0, 50, 200];
  const SPEED_MODIFIERS = [1, 1.25, 1.5];

  const processOutfit = () => {
    for (let i = 0; i < OUTFIT_COIN_REQUIRED.length; i++) {
      if (outfit >= i) {
        continue;
      }
      if (coinCount > OUTFIT_COIN_REQUIRED[i]) {
        outfit = i;
        transitionTime = stateTime;
        speedY = NORMAL_SPEED_Y * SPEED_MODIFIERS[i];
      }
    }
  };

  const MAXIMUM_DEADLINE_RATE = 0.015;
  const obstacles = [];
  const watches = [];

  const PROJECTILE_SPEED = width;
  const PROJECTILE_SIZE = width / 7.5;

  const deadlineRenderSystem = createDeadlineRenderSystem(width, height, deadLineAtlas, batch);
  const bgRenderSystem = createBgRenderSystem(width, height, bg1, bg2, batch);
  const particleSystem = createParticleSystem(width, height, noteTex, batch, PLAYER_SIZE);
  const wallSystem = createWallSystem(width, height, wallStarts, obstacles, WALL_LENGTH, WALL_THICKNESS);
  const wallRenderSystem = createWallRenderSystem(width, height, whiteTex, batch, wallStarts, WALL_LENGTH, WALL_THICKNESS);
  const playerRenderSystem = createPlayerRenderSystem(width, height, batch, characterAtlas, slashAtlas, obstacleAtlas, watchAtlas, PLAYER_SIZE, gender, outfit);
  const obstacleSystem = createObstacleSystem(width, height, obstacles, WALL_LENGTH, WALL_THICKNESS, PROJECTILE_SPEED, PROJECTILE_SIZE);
  const obstacleRenderSystem = createObstacleRenderSystem(whiteTex, batch, obstacles, obstacleAtlas);
  const debuffRenderSystem = createDebuffRenderSystem(width, height, whiteTex, batch, debuffs, obstacleAtlas, gender, outfit);
  const watchSystem = createWatchSystem(width, height, watches, outfit);
  const watchRenderSystem = createWatchRenderSystem(width, height, batch, watchAtlas, watches, PLAYER_SIZE);
  const CoinRender = renderCoin(Game, coinCount);
  const buffRender = createBuffListRenderSystem(Game);

  let stateTime = 0;

  const check_lost = () => {
    if (gap >= height * 0.96) {
      return true;
    }
    return false;
  };

  const update = async delta => {
    if (!check_lost()) {
      if (hasBuff() || hasSpecial()) {
        debuffs.length = 0;
      }
      gap = gap + (targetGap - gap) * 0.02;
      for (let i = debuffs.length - 1; i >= 0; i--) {
        if (stateTime - debuffs[i].time >= debuffs[i].duration) {
          debuffs.splice(i, 1);
        }
      }
      stateTime += delta;
      particleSystem.update(delta, x, y, speedY);
      processPlayerPos(delta);
      processObstacleCollision(delta);
      processWatchCollsion(delta);
      processOutfit();
      obstacleSystem.update(delta, y, stateTime);
      wallSystem.update(delta, y, gap);
      watchSystem.update(delta, stateTime, y, outfit);

      camera.setPosition(0, 0);
      batch.setProjection(camera.combined);
      batch.begin();
      bgRenderSystem.update(delta, y, gap);
      deadlineRenderSystem.update(delta, stateTime);
      if (buffs.length === 3) {
        specialButton(batch, whiteTex, width, height, assets);
      }
      batch.end();

      buffRender.update(buffs);
      CoinRender.update(coinCount);

      camera.setPosition(0, y - gap);
      batch.setProjection(camera.combined);
      batch.begin();

      particleSystem.draw();
      playerRenderSystem.draw(x, y, flipped, jumping, stateTime, jumpStart, buffTime, outfit, transitionTime, specialTime);
      watchRenderSystem.draw(stateTime, x, y);
      wallRenderSystem.draw();
      obstacleRenderSystem.draw(stateTime);
      debuffRenderSystem.draw(stateTime, y);

      batch.end();
    } else {
      Game.setCurrentScreen(await createEndScreen(Game, coinCount, y, watchCount));
    }
  };

  return createScreen({
    update,
    destroy() {
      inputHandler.cleanup();
      document.removeEventListener('keydown', jump);
    }
  });
};
