import { fillRect } from 'gdxjs';

export const specialButton = (batch, whiteTex, width, height, assets) => {
  const button = assets.button;
  const buttonR = width / 3.8;
  batch.setColor(1, 1, 1, 1);
  fillRect(batch, button, width / 2 - buttonR / 2, (height * 4.7) / 6, buttonR, buttonR);
};
