import createScreen from '../util/createScreen';
import { createGenderChoosingScreen } from './genderChoosingScreen';
import { createBatch, InputHandler, createOrthoCamera, fillRect } from 'gdxjs';
import { AlignMode } from '../util/loadFontFile';
import createWhiteTex from 'gl-white-texture';
import { checkPointInsideRect } from '../util/coordUtil';

// const coint_list = [0, 50, 200];

export const createEndScreen = async (Game, coinCount, distance, watch) => {
  const goToChoosingScreen = async () => {
    Game.setCurrentScreen(await createGenderChoosingScreen(Game));
  };
  const { gl, width, height, canvas, font, gender, pixelRatio } = Game;
  const endScreen = Game.assets.endScreen;
  const { achive1, achive2, achive3, characterAtlas } = Game.assets;
  const whiteTex = createWhiteTex(gl);

  const inputHandler = new InputHandler(canvas);
  const batch = createBatch(gl);
  const camera = createOrthoCamera(width, height, width, height);
  // inputHandler.addEventListener('touchStart', (x, y) => {
  //   goToChoosingScreen();
  // });

  const retry = Game.assets.retry;
  const reward = Game.assets.reward;
  const buttonW = width / 2.5;
  const buttonH = (buttonW * retry.height) / retry.width;

  const girl = characterAtlas.findRegion('girl_running_1', 0);
  const man = characterAtlas.findRegion('man_running_1', 0);
  const girl_size = [width / 7, ((width / 7) * girl.height) / girl.width];
  const man_size = [width / 7, ((width / 7) * man.height) / man.width];

  const coin = font.setNewText(width);
  const lengthHaveGone = font.setNewText(width);
  const watchCount = font.setNewText(width);
  const eScreenW = width;
  const eScreenH = (eScreenW * endScreen.height) / endScreen.width;

  const achiveW = width / 20;
  const achiveH = (achiveW * achive1.height) / achive1.width;

  let ratio_list = [coinCount / 50, (coinCount - 50) / 150, (coinCount - 200) / 200];
  for (let index in ratio_list) {
    if (ratio_list[index] > 1) {
      ratio_list[index] = 1;
    }
  }
  if (coinCount <= 50) {
    ratio_list[1] = ratio_list[2] = 0;
  } else if (coinCount <= 200 && coinCount > 50) {
    ratio_list[2] = 0;
  } else if (coinCount === 400) {
    ratio_list[2] = 1;
  }
  const rest_ratio = [
    height / 4.5 + (height / 6) * 2 + height / 90 + (height / 6) * (1 - ratio_list[0]),
    height / 4.5 + height / 6 + height / 180 + (height / 6) * (1 - ratio_list[1]),
    height / 4.5 + (height / 6) * (1 - ratio_list[2])
  ];
  let stateTime = 0;
  let startCount = true;
  return createScreen({
    update(delta) {
      // fillRect(batch, reward, (width * 0.91) / 2, height / 2, buttonW, buttonH);
      // fillRect(batch, retry, (width * 0.91) / 2, height / 2 + buttonH + width / 50, buttonW, buttonH);
      const link_rect = [
        (width * 0.91) / 2,
        height / 2,
        (width * 0.91) / 2 + buttonW,
        height / 2,
        (width * 0.91) / 2,
        height / 2 + buttonH,
        (width * 0.91) / 2 + buttonW,
        height / 2 + buttonH
      ];

      const reset_rect = [
        (width * 0.91) / 2,
        height / 2 + buttonH + width / 50,
        (width * 0.91) / 2 + buttonW,
        height / 2 + buttonH + width / 50,
        (width * 0.91) / 2,
        height / 2 + buttonH + width / 50 + buttonH,
        (width * 0.91) / 2 + buttonW,
        height / 2 + buttonH + width / 50 + buttonH
      ];
      if (startCount) {
        stateTime += delta;
      }
      if (stateTime >= 1) {
        stateTime = 0;
        inputHandler.addEventListener('touchStart', (x, y) => {
          let point = { x: x, y: y };
          if (checkPointInsideRect(point, link_rect, pixelRatio)) {
            startCount = false;
            window.location.href = 'https://curnonwatch.com/';
          }
          if (checkPointInsideRect(point, reset_rect, pixelRatio)) {
            startCount = false;
            goToChoosingScreen();
          }
        });
      }

      camera.setPosition(0, 0);
      batch.setProjection(camera.combined);
      batch.begin();
      batch.setColor(1, 1, 1, 1);
      batch.draw(endScreen, width / 2 - eScreenW / 2, height / 2 - eScreenH / 2, eScreenW, eScreenH);
      batch.draw(achive1, width / 4.1 - achiveW * 1.3, height / 4.5 + (height / 6) * 2 + height / 60 - achiveH / 2, achiveW, achiveH);
      batch.draw(achive2, width / 4.1 - achiveW * 1.3, height / 4.5 + height / 6 + height / 120 - achiveH / 2, achiveW, achiveH);
      batch.draw(achive3, width / 4.1 - achiveW * 1.3, height / 4.5 - achiveH / 2, achiveW, achiveH);

      batch.setColor(0.87, 0.87, 0.87, 1);
      batch.draw(whiteTex, width / 4.1, height / 4.5, width / 38, height / 6);
      batch.draw(whiteTex, width / 4.1, height / 4.5 + height / 6 + height / 180, width / 38, height / 6);
      batch.draw(whiteTex, width / 4.1, height / 4.5 + (height / 6) * 2 + height / 90, width / 38, height / 6);

      batch.setColor(1, 0.5, 0, 1);
      batch.draw(whiteTex, width / 4.1, rest_ratio[0], width / 38, (height / 6) * ratio_list[0]);
      batch.draw(whiteTex, width / 4.1, rest_ratio[1], width / 38, (height / 6) * ratio_list[1]);
      batch.draw(whiteTex, width / 4.1, rest_ratio[2], width / 38, (height / 6) * ratio_list[2]);

      batch.setColor(0, 0, 0, 1);
      coin.setAlignMode(AlignMode.left);
      coin.draw(batch, `x${coinCount}`, (width * 1.71) / 3, height / 2.65, width / 20);
      watchCount.setAlignMode(AlignMode.center);
      watchCount.draw(batch, `x${watch}`, (width * 2.2) / 3, height / 3.1, width / 20);
      lengthHaveGone.setAlignMode(AlignMode.center);
      lengthHaveGone.draw(batch, `${Math.floor((Math.floor(distance) * -1) / 10)}m`, (width * 1.94) / 3, height / 2.24, width / 14);
      batch.setColor(1, 1, 1, 1);
      fillRect(batch, reward, (width * 0.91) / 2, height / 2, buttonW, buttonH);
      fillRect(batch, retry, (width * 0.91) / 2, height / 2 + buttonH + width / 50, buttonW, buttonH);
      // reward.draw(batch, width / 2, height / 2, buttonW, buttonH);

      // batch.setColor(1, 1, 0, 1);
      // // batch.draw(whiteTex, (width * 1.38) / 3, (height * 1.55) / 3, width / 2.65, height / 15);
      // batch.draw(whiteTex, (width * 0.94) / 3, (height * 2.56) / 4, width / 2.65, width / 28);

      // draw charactor
      batch.setColor(1, 1, 1, 1);
      let girlY;
      let manY;
      if (coinCount <= 50) {
        girlY = rest_ratio[0];
        manY = rest_ratio[0];
      } else if (coinCount > 50 && coinCount <= 200) {
        girlY = rest_ratio[1];
        manY = rest_ratio[1];
      } else {
        girlY = rest_ratio[2];
        manY = rest_ratio[2];
      }

      if (gender === 0) {
        man.draw(batch, width / 3.6, manY - man_size[1] / 2, man_size[0], man_size[1], man_size[0] / 2, man_size[1] / 2, 0, -1, 1);
      } else {
        girl.draw(batch, width / 3.6, girlY - girl_size[1] / 2, girl_size[0], girl_size[1], girl_size[0] / 2, girl_size[1] / 2, 0, -1, 1);
      }
      batch.end();
    },
    destroy() {
      inputHandler.cleanup();
      // document.removeEventListener('keydown', goToGameScreen);
    }
  });
};
