import { createAnimation, fillRect } from 'gdxjs';
import { PlayMode } from 'gdxjs/lib/createAnimation';

export const createDebuffRenderSystem = (width, height, whiteTex, batch, debuffs, obstacleAtlas, gender, outfit) => {
  const obstacleHitBaseName = `ob_${gender === 0 ? 'man' : 'girl'}`;

  const OBSTACLE_HIT_TYPE_NAME = [null, `${obstacleHitBaseName}_2`, `${obstacleHitBaseName}_3`, `${obstacleHitBaseName}_1`];

  const sampleFrame = obstacleAtlas.findRegion('ob_man_1');

  const DEBUFF_WIDTH = width / 2;
  const DEBUFF_HEIGHT = (DEBUFF_WIDTH * sampleFrame.height) / sampleFrame.width;

  const obstacleHitAnimations = [];
  for (let i = 0; i < OBSTACLE_HIT_TYPE_NAME.length; i++) {
    if (i === 0) continue;
    const regions = obstacleAtlas.findRegions(OBSTACLE_HIT_TYPE_NAME[i]);
    obstacleHitAnimations[i] = createAnimation(0.05, regions);
  }

  return {
    draw(stateTime, y) {
      if (debuffs.length > 0) {
        batch.setColor(0, 0, 0, 0.5);
        fillRect(batch, whiteTex, 0, y - height, width, height * 2);
        batch.setColor(1, 1, 1, 1);
      }
      for (let debuff of debuffs) {
        if (debuff.type === 0) continue;
        const animation = obstacleHitAnimations[debuff.type];
        animation
          .getKeyFrame(stateTime - debuff.time, PlayMode.NORMAL)
          .draw(
            batch,
            debuff.x - DEBUFF_WIDTH / 2,
            y + 2 * debuff.offsetY - DEBUFF_HEIGHT / 2,
            DEBUFF_WIDTH,
            DEBUFF_HEIGHT,
            DEBUFF_WIDTH / 2,
            DEBUFF_HEIGHT / 2,
            0,
            debuff.flipped ? 1 : -1,
            1
          );
      }
    }
  };
};
