import { Vector2 } from 'gdxjs';

export const createObstacleSystem = (width, height, obstacles, WALL_LENGTH, WALL_THICKNESS, PROJECTILE_SPEED, PROJECTILE_SIZE) => {
  const tmp = new Vector2();

  return {
    update(delta, y, stateTime) {
      for (let obstacle of obstacles) {
        if (obstacle.type > 0 && !obstacle.fired && Math.abs(obstacle.y - y) < WALL_LENGTH) {
          obstacle.fired = true;
          obstacle.projectilePos = new Vector2(obstacle.wallIndex === 0 ? WALL_THICKNESS : width - WALL_THICKNESS, obstacle.y);
          const vel = new Vector2();
          vel.set(obstacle.wallIndex === 0 ? PROJECTILE_SPEED : -PROJECTILE_SPEED, 0);
          const rotation = -(Math.random() * Math.PI) / 6 - Math.PI / 18;
          vel.rotateRad(obstacle.wallIndex === 0 ? rotation : -rotation);
          obstacle.projectileVel = vel;
          obstacle.projectileSize = PROJECTILE_SIZE;
          obstacle.projectileStart = stateTime;
        }
        if (obstacle.fired) {
          tmp.setVector(obstacle.projectileVel).scale(delta);
          obstacle.projectilePos.addVector(tmp);
        }
      }
    }
  };
};
