import { createAnimation } from 'gdxjs';
import { PlayMode } from 'gdxjs/lib/createAnimation';

export const createWatchRenderSystem = (width, height, batch, watchAtlas, watches, PLAYER_SIZE) => {
  const watchAnimations = [];
  for (let i = 0; i < 3; i++) {
    watchAnimations.push(createAnimation(0.1, watchAtlas.findRegions(`watch_${i + 1}`)));
  }
  return {
    draw(stateTime, x, y) {
      for (let watch of watches) {
        if (stateTime - watch.hitTime > 0.5) {
          continue;
        }
        if (watch.hit) {
          watch.drawX = watch.drawX + (x + PLAYER_SIZE / 2 - watch.drawX) * 0.2;
          watch.drawY = watch.drawY + (y - watch.drawY) * 0.2;
          watch.drawScale = watch.drawScale + (0.5 - watch.drawScale) * 0.2;
        }
        watchAnimations[watch.type]
          .getKeyFrame(stateTime, PlayMode.LOOP)
          .draw(
            batch,
            watch.drawX - watch.width / 2,
            watch.drawY - watch.height / 2,
            watch.width,
            watch.height,
            watch.width / 2,
            watch.height / 2,
            0,
            watch.drawScale,
            watch.drawScale
          );
      }
    }
  };
};
