const results = [];

export const calculateParallaxByOffset = (displaySize, texSize, offset) => {
  results.length = 0;
  let current = offset;
  while (current > displaySize) {
    current -= texSize;
  }
  while (current < -texSize) {
    current += texSize;
  }
  const startPoint = current;
  do {
    results.push(current);
    current += texSize;
  } while (current < displaySize);
  current = startPoint;
  while (current > 0) {
    current -= texSize;
    results.push(current);
  }
  return results;
};

export const calculateParallaxBySpeed = (
  stateTime,
  displaySize,
  texSize,
  speed
) => {
  const offset = speed * stateTime;
  return calculateParallaxByOffset(displaySize, texSize, offset);
};
