import { createAnimation } from 'gdxjs';
import { PlayMode } from 'gdxjs/lib/createAnimation';

export const createDeadlineRenderSystem = (width, height, deadLineAtlas, batch) => {
  const deadlineBackGradient = deadLineAtlas.findRegion('deadline-back-gradient');
  const deadlineFrontGradient = deadLineAtlas.findRegion('deadline-front-gradient');
  const singleNote = deadLineAtlas.findRegion('single-note');
  const singlePaper = deadLineAtlas.findRegion('single-paper');
  const deadlineBase = deadLineAtlas.findRegion('deadline-base');
  const singlePaperStackFrame = deadLineAtlas.findRegion('paper_stack');
  const paperStackAnimation = createAnimation(0.1, deadLineAtlas.findRegions('paper_stack'));

  const PARTICLE_SIZE = width / 30;
  const BG_WIDTH = width;
  const BACK_GRADIENT_HEIGHT = (BG_WIDTH / deadlineBackGradient.width) * deadlineBackGradient.height;
  const BACK_GRADIENT_Y = height - BACK_GRADIENT_HEIGHT;
  const BASE_HEIGHT = (BG_WIDTH / deadlineBase.width) * deadlineBase.height;
  const BASE_Y = height - BASE_HEIGHT / 2;
  const FRONT_GRADIENT_HEIGHT = (BG_WIDTH / deadlineFrontGradient.width) * deadlineFrontGradient.height;
  const FRONT_GRADIENT_Y = height - FRONT_GRADIENT_HEIGHT;

  const PAPER_STACK_COUNT = 4;
  const PAPER_STACK_WIDTH = width / PAPER_STACK_COUNT;
  const PAPER_STACK_HEIGHT = (PAPER_STACK_WIDTH / singlePaperStackFrame.width) * singlePaperStackFrame.height;
  const PAPER_STACK_Y = height - PAPER_STACK_HEIGHT;
  const PAPER_STACK_ANIMATION_DELAY = [];
  for (let i = 0; i < PAPER_STACK_COUNT; i++) {
    PAPER_STACK_ANIMATION_DELAY[i] = Math.random() * 0.5;
  }

  let deadlineParticleTime = 0;
  let deadLineParticleRate = 0.15;
  const deadlineParticles = [];
  const DEADLINE_PARTICLE_SPEED = -height / 2;
  const DEADLINE_PARTICLE_DECELERATOR = height * 0.75;

  return {
    setDeadLineParticleRate(rate) {
      deadLineParticleRate = rate;
    },
    getDeadLineParticleRate() {
      return deadLineParticleRate;
    },
    update(delta, stateTime) {
      deadlineParticleTime += delta;
      while (deadlineParticleTime >= deadLineParticleRate) {
        deadlineParticleTime -= deadLineParticleRate;
        const type = Math.random() > 0.5 ? 0 : 1;
        deadlineParticles.push({
          x: Math.random() * width,
          y: height,
          size: type === 0 ? PARTICLE_SIZE * 2 : PARTICLE_SIZE,
          speedY: DEADLINE_PARTICLE_SPEED,
          type,
          rotation: (Math.random() * Math.PI) / 2
        });
      }

      for (let particle of deadlineParticles) {
        particle.speedY += DEADLINE_PARTICLE_DECELERATOR * delta;
        particle.y = particle.y + particle.speedY * delta;
        if (particle.y > height) {
          particle.expired = true;
        }
      }

      for (let i = deadlineParticles.length - 1; i >= 0; i--) {
        if (deadlineParticles[i].expired) {
          deadlineParticles.splice(i, 1);
        }
      }

      deadlineBackGradient.draw(batch, 0, BACK_GRADIENT_Y, BG_WIDTH, BACK_GRADIENT_Y);
      deadlineBase.draw(batch, 0, BASE_Y, BG_WIDTH, BASE_HEIGHT);
      for (let i = 0; i < PAPER_STACK_COUNT - 1; i++) {
        paperStackAnimation
          .getKeyFrame(stateTime + PAPER_STACK_ANIMATION_DELAY[i], PlayMode.LOOP_PINGPONG)
          .draw(batch, PAPER_STACK_WIDTH * i + PAPER_STACK_WIDTH / 2, PAPER_STACK_Y - PAPER_STACK_HEIGHT / 8, PAPER_STACK_WIDTH, PAPER_STACK_HEIGHT);
      }
      for (let particle of deadlineParticles) {
        (particle.type === 0 ? singlePaper : singleNote).draw(
          batch,
          particle.x,
          particle.y,
          particle.size,
          particle.size,
          particle.size / 2,
          particle.size / 2,
          particle.rotation
        );
      }
      for (let i = 0; i < PAPER_STACK_COUNT; i++) {
        paperStackAnimation
          .getKeyFrame(stateTime + PAPER_STACK_ANIMATION_DELAY[i], PlayMode.LOOP_PINGPONG)
          .draw(batch, PAPER_STACK_WIDTH * i, PAPER_STACK_Y, PAPER_STACK_WIDTH, PAPER_STACK_HEIGHT);
      }
      deadlineFrontGradient.draw(batch, 0, FRONT_GRADIENT_Y, BG_WIDTH, FRONT_GRADIENT_HEIGHT);
    }
  };
};
