import createScreen from '../util/createScreen';
import { createInstructionScreen } from './genderInstructionScreen';
import { InputHandler, createBatch, createOrthoCamera, fillRect } from 'gdxjs';
import { checkPointInsideRect } from '../util/coordUtil';
import { AlignMode, TextType } from '../util/loadFontFile';
import createWhiteTexture from 'gl-white-texture';

export const createGenderChoosingScreen = Game => {
  const goToInstructionScreen = async () => Game.setCurrentScreen(await createInstructionScreen(Game));
  // document.addEventListener('keydown', goToInstructionScreen);

  const { gl, width, height, assets, canvas, title, pixelRatio } = Game;
  const newText = title.setNewText(width);

  const batch = createBatch(gl);
  const camera = createOrthoCamera(width, height, width, height);
  const inputHandler = new InputHandler(canvas);

  const whiteTex = createWhiteTexture(gl);

  const { woman, man, tray, bg2, bg1 } = assets;
  const BG_WIDTH = width;
  const BG_HEIGHT = (BG_WIDTH / bg1.width) * bg1.height;
  const BG2_HEIGHT = (BG_WIDTH / bg2.width) * bg2.height;
  const HUMAN_HEIGHT = height / 4;
  const WOMAN_WIDTH = (HUMAN_HEIGHT * woman.width) / woman.height;
  const MAN_WIDTH = (HUMAN_HEIGHT * man.width) / man.height;
  const TRAY_WIDTH = width / 4;
  const TRAY_HEIGHT = (TRAY_WIDTH * tray.height) / tray.width;

  const trayX = width / 2 - TRAY_WIDTH / 2;
  const trayY = (height * 2.2) / 3;

  const man_rect = [
    trayX - width / 5 + MAN_WIDTH / 2,
    (height * 1.46) / 3,
    trayX - width / 5 + TRAY_WIDTH / 2 + MAN_WIDTH,
    (height * 1.46) / 3,
    trayX - width / 5 + TRAY_WIDTH / 2,
    (height * 1.46) / 3 + HUMAN_HEIGHT,
    trayX - width / 5 + TRAY_WIDTH / 2 + MAN_WIDTH,
    (height * 1.46) / 3 + HUMAN_HEIGHT
  ];
  const woman_rect = [
    trayX + width / 5 + TRAY_WIDTH / 2 - WOMAN_WIDTH / 2,
    (height * 1.46) / 3,
    trayX + width / 5 + TRAY_WIDTH / 2 - WOMAN_WIDTH / 2 + WOMAN_WIDTH,
    (height * 1.46) / 3,
    trayX + width / 5 + TRAY_WIDTH / 2 - WOMAN_WIDTH / 2,
    (height * 1.46) / 3 + HUMAN_HEIGHT,
    trayX + width / 5 + TRAY_WIDTH / 2 - WOMAN_WIDTH / 2 + WOMAN_WIDTH,
    (height * 1.46) / 3 + HUMAN_HEIGHT
  ];
  let point;
  inputHandler.addEventListener('touchStart', (x, y) => {
    point = { x: x, y: y };
    if (checkPointInsideRect(point, man_rect, pixelRatio)) {
      Game.gender = 0;
      goToInstructionScreen();
    }
    if (checkPointInsideRect(point, woman_rect, pixelRatio)) {
      Game.gender = 1;
      goToInstructionScreen();
    }
  });

  return createScreen({
    update(delta) {
      camera.setPosition(0, 0);
      batch.setProjection(camera.combined);
      batch.begin();

      batch.draw(bg2, 0, 0, BG_WIDTH, BG2_HEIGHT);
      batch.draw(bg1, 0, 0, BG_WIDTH, BG_HEIGHT);
      batch.setColor(1, 1, 1, 0.5);
      fillRect(batch, whiteTex, 0, 0, width, height);
      batch.setColor(1, 1, 1, 1);
      newText.setAlignMode(AlignMode.center);
      newText.setType(TextType.dynamic);
      newText.setFixedGlyphWidth(width);
      // batch.setColor(0, 51 / 255, 102 / 255, 1);
      batch.setColor(0, 0, 0, 1);
      newText.draw(batch, 'CHN NHN VT', width / 2, trayY - width / 8 - HUMAN_HEIGHT, width / 20);

      batch.setColor(1, 1, 1, 1);
      batch.draw(man, trayX - width / 5 + MAN_WIDTH / 2, (height * 1.46) / 3, MAN_WIDTH, HUMAN_HEIGHT);
      batch.draw(woman, trayX + width / 5 + TRAY_WIDTH / 2 - WOMAN_WIDTH / 2, (height * 1.46) / 3, WOMAN_WIDTH, HUMAN_HEIGHT);
      batch.draw(tray, trayX - width / 5, trayY, TRAY_WIDTH, TRAY_HEIGHT);
      batch.draw(tray, trayX + width / 5, trayY, TRAY_WIDTH, TRAY_HEIGHT);
      batch.end();
    },
    destroy() {
      inputHandler.cleanup();
    }
  });
};
