import { calculateParallaxByOffset } from '../../util/parallaxCalculator';

export const createBgRenderSystem = (width, height, bg1, bg2, batch) => {
  const BG_WIDTH = width;
  const BG1_HEIGHT = (BG_WIDTH / bg1.width) * bg1.height;
  const BG2_HEIGHT = (BG_WIDTH / bg2.width) * bg2.height;

  return {
    update(delta, y, gap) {
      let drawStarts = calculateParallaxByOffset(height, BG2_HEIGHT, (-y + gap) * 0.5);
      for (let drawStart of drawStarts) {
        batch.draw(bg2, 0, drawStart, BG_WIDTH, BG2_HEIGHT);
      }

      drawStarts = calculateParallaxByOffset(height, BG1_HEIGHT, -y + gap);
      for (let drawStart of drawStarts) {
        batch.draw(bg1, 0, drawStart, BG_WIDTH, BG1_HEIGHT);
      }
    }
  };
};
