export const createWatchSystem = (width, height, watches) => {
  const WATCH_SIZE = width / 5;
  let timeUntilNextWatch = 5;
  let timeRandom = 0;
  return {
    update(delta, stateTime, y, outfit) {
      timeUntilNextWatch -= delta;
      if (timeUntilNextWatch <= 0) {
        switch (outfit) {
          case 0:
            timeRandom = Math.random() * 10 - 6 + 6;
            break;
          case 1:
            timeRandom = Math.random() * 6 - 4 + 4;
            break;
          case 2:
            timeRandom = Math.random() * 10 - 4 + 4;
            break;
          default:
            break;
        }
        timeUntilNextWatch = timeRandom;
        watches.push({
          x: width / 2,
          y: y - height,
          drawX: width / 2,
          drawY: y - height,
          drawScale: 1,
          time: stateTime,
          width: WATCH_SIZE,
          height: WATCH_SIZE,
          type: Math.floor(Math.random() * 3)
        });
      }
      for (let i = watches.length - 1; i >= 0; i--) {
        if (watches[i].y > y + 2 * height) {
          watches.splice(i, 1);
        }
      }
    }
  };
};
