export const createParticleSystem = (width, _height, noteTex, batch, PLAYER_SIZE) => {
  const particles = [];
  let particleTime = 0;
  const PARTICLE_RATE = 0.075;
  const PARTICLE_DURATION = 3;
  const PARTICLE_SIZE = width / 30;
  const PARTICLE_RANGE = width / 7.5;

  return {
    update(delta, x, y, speedY) {
      particleTime += delta;
      while (particleTime >= PARTICLE_RATE) {
        particleTime -= PARTICLE_RATE;
        particles.push({
          x: x + PLAYER_SIZE / 2 + (Math.random() - 0.5) * PARTICLE_RANGE,
          y: y + PLAYER_SIZE / 2 + (Math.random() - 0.5) * PARTICLE_RANGE,
          speedY: speedY / 2,
          type: Math.random() < 0.3 ? 0 : Math.random() < 0.5 ? 1 : 2,
          rotation: (Math.random() * Math.PI) / 2,
          duration: Math.random() * PARTICLE_DURATION,
          t: 0,
          size: PARTICLE_SIZE
        });
      }

      for (let particle of particles) {
        particle.t += delta;
        particle.y += particle.speedY * delta;
      }
      for (let i = particles.length - 1; i >= 0; i--) {
        const particle = particles[i];
        if (particle.t >= particle.duration) {
          particles.splice(i, 1);
        }
      }
    },
    draw() {
      for (let particle of particles) {
        let alpha = (particle.duration - particle.t) / particle.duration;
        alpha = alpha * alpha * (3 - 2 * alpha);
        batch.setColor(0, 0, 0, 0.75 * alpha);
        batch.draw(
          noteTex,
          particle.x - particle.size / 2 + particle.size / 5,
          particle.y - particle.size / 2 + particle.size / 5,
          particle.size,
          particle.size,
          particle.size / 2,
          particle.size / 2,
          particle.rotation
        );
        particle.type === 0 && batch.setColor(1, 0.494, 0.725, alpha);
        particle.type === 1 && batch.setColor(0.478, 0.988, 1, alpha);
        particle.type === 2 && batch.setColor(1, 0.969, 0.251, alpha);
        batch.draw(
          noteTex,
          particle.x - particle.size / 2,
          particle.y - particle.size / 2,
          particle.size,
          particle.size,
          particle.size / 2,
          particle.size / 2,
          particle.rotation
        );
        batch.setColor(1, 1, 1, 1);
      }
    }
  };
};
