export const createWallSystem = (width, height, wallStarts, obstacles, WALL_LENGTH, WALL_THICKNESS, gap) => {
  let latestWallStartGenerated = WALL_LENGTH;
  const obstacleChance = 0.4;
  const OBSTACLE_SIZE = width / 10;

  return {
    update(delta, y, gap) {
      const screenY = y + gap;
      for (let i = wallStarts.length - 1; i >= 0; i--) {
        const wallStart = wallStarts[i];
        if (wallStart - WALL_LENGTH > screenY) {
          wallStarts.splice(i, 1);
        }
      }
      for (let i = obstacles.length - 1; i >= 0; i--) {
        const obstacle = obstacles[i];
        if (obstacle.y > wallStarts[0]) {
          obstacles.splice(i, 1);
        }
      }
      while (latestWallStartGenerated > screenY - 2 * height - WALL_LENGTH) {
        latestWallStartGenerated -= WALL_LENGTH;
        wallStarts.push(latestWallStartGenerated);
        if (latestWallStartGenerated > -height) {
          continue;
        }
        if (Math.random() <= obstacleChance) {
          const type = Math.floor(Math.random() * 4);
          const wallIndex = Math.random() > 0.5 ? 0 : 1;
          const obstacleWidth = type === 0 ? OBSTACLE_SIZE * 3 : OBSTACLE_SIZE * 2;
          const obstacleHeight = type === 0 ? OBSTACLE_SIZE / 2 : OBSTACLE_SIZE * 2;
          obstacles.push({
            wallIndex,
            y: type === 0 ? latestWallStartGenerated - WALL_LENGTH : latestWallStartGenerated - WALL_LENGTH - obstacleHeight * 0.9,
            type,
            width: obstacleWidth,
            height: obstacleHeight,
            x: wallIndex === 0 ? WALL_THICKNESS : width - WALL_THICKNESS - obstacleWidth
          });
        }
      }
    }
  };
};
